import React, {Component} from 'react'
import {
	Button,
	Container,
	Row,
	Col,
	Card,
	CardTitle,
	CardSubtitle,
	CardBody
} from 'reactstrap'

import Navbar from './navbar.jsx'

export default class Home extends Component {
	constructor(props) {
		super(props)
		this.state = {
			dataLog : [],
			scroll : true,
			btnScroll : 'Stop Scroll'
		}
	}

	componentDidMount() {
		cSocket.on('log', (data) => {
			if (data) {
				this.setState({
					dataLog : this.state.dataLog.concat([{date : data.date, message : data.message}])
				})
				this._scrollBottom()
			}
		})
	}

	componentDidUpdate() {
		this._scrollBottom()
	}

	_scrollBottom() {
		if (this.state.scroll) {
			this.el.scrollTop = this.el.scrollHeight
		}
	}

	_renderLog() {
		if (this.state.dataLog.length <= 0) {
			return (
				<div>
					<center>
						<b>No Log Record</b>
					</center>
				</div>
			)
		} else {
			return this.state.dataLog.map((log, index) => {
				return (
					<div key={index} tabIndex={index}>
						<span>
							<b>{log.date}</b> - {log.message}
						</span>
					</div>
				)
			})
		}
	}

	_stopScroll() {
		if (this.state.scroll) {
			this.setState({
				scroll : false,
				btnScroll : 'Enable Smart Scroll'
			})
		} else {
			this.setState({
				scroll : true,
				btnScroll : 'Stop Scroll'
			})
		}
	}
	render() {
		return (
			<Container>
				<Row style={{marginTop: 10}}>
					<Col md="1"></Col>
					<Col md="10">
						<div className="panel panel-primary">
							<div className="panel-heading">
								<h4>Hilsa Log</h4>
								<span>A Magic Realtime Log Application</span>
							</div>
							<div
								className="panel-body"
								style={{overflowY : 'auto', maxHeight : '400px'}}
								id="panel-log"
								ref={el => {this.el = el}}>
								{this._renderLog()}
							</div>
							
							<div className="panel-footer">
								<button className="btn btn-default" onClick={() => this._stopScroll()} ref={'buttonStop'}>
									{this.state.btnScroll}
								</button>
								<center>
									<small>
										<a href="javascript:void(0)" title="Hisa Log">Hilsa Log</a> By Creativefull 2016
									</small>
								</center>
							</div>
						</div>
					</Col>
				</Row>
			</Container>
		)
	}
}


